import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";
import './App.css';
import firebase from './firebase';
import Spinner from 'react-bootstrap/Spinner';

// import firebase from './firebase';

const Sponsor = () => {
    const history = useHistory();

  useEffect(() => {        
    begin();    
  });

  async function begin() {
    console.log("starting up");
    // get familys
    let families = await firebase.getFamilies();
    let html = "";
    for (let f = 0; f < families.length; f++) {
        if (f < 10) {
            // max 10 families at a time
            let right = "";
            if (f % 2 !== 0) {
                right = " sponsorCardRight";
            }
            html += `
                <div class="sponsorCard` + right + `">
                    <img src="` + families[f].image + `" alt="sponsor" />
                    <div class="text">
                        <b>` + families[f].family + `</b>
                        <p>` + families[f].desc + `</p>
                        <button class='sponsorBtn btn btn-primary' value='` + families[f].id + `'>Sponsor ` + families[f].family + ` for $` + families[f].amount + ` a month</button>
                    </div>                
                </div>
            `;
        } else {
            f = families.length;
        }
    }

    let cards = document.getElementsByClassName("sponsorCard");
    for (let i = 1; i < cards.length; i += 2) {
        document.getElementsByClassName("sponsorCard")[i].classList.add("sponsorCardRight");
    }

    document.getElementById("familiesDiv").innerHTML = html;
    document.getElementsByClassName("spinner")[0].style.display = "none";

    await setSponsorBtns();
  }

  async function setSponsorBtns() {
    let btns = document.getElementsByClassName("sponsorBtn");
    for (let i = 0; i < btns.length; i++) {
        btns[i].onclick = async function(){
            history.push("/sponsorFamily?family=" + btns[i].value);
            // await firebase.validateOrder(btns[i].value)
        }
    }
  }

  return (
    <div className="Sponsor">
        <div className="content">
            <br />
            <h4>Sponsor a family</h4>
            <br />
            <p>With your donations, you will receive updates on the wellbeing of your chosen family.</p>
            <br />
            <Spinner animation="border" className="spinner" />
            <div id="familiesDiv"></div>
        </div>
    </div>
  );
}

export default Sponsor;
