import React, {useEffect} from 'react';
import { useHistory } from "react-router-dom";
import './App.css';
import PaymentSection from './paymentSection';
import firebase from './firebase';
// import firebase from './firebase';

const SponsorFamily = () => {
  const history = useHistory();

  useEffect(() => {        
    begin();    
  });

  async function begin() {
    console.log("starting up");
    let url = window.location.href;
    let code = url.split("?family=");
    let ref = "";
    if (code.length > 1) {
        code = code[1];
        let families = await firebase.getFamilies();
        let passed = false;
        for (let f = 0; f < families.length; f++) {
          if (parseInt(code) === families[f].id) {
            passed = true;
            ref = "Sponsor " + families[f].family + " - $" + families[f].amount + " a month";
            f = families.length;
            document.getElementById("familyID").innerHTML = ref;
            document.getElementById("reference01").value = ref;
            document.getElementById("reference01").readOnly = true;
          }
        }
        if (passed === false) {
          history.push("/");
        }
    }
  }

  async function setSponsored() {
    let url = window.location.href;
    let code = url.split("?family=");
    if (code.length > 1) {
        code = code[1];
        await firebase.setSponsored(parseInt(code));
        addNotification(`alert alert-success`, `Success`);

    }
  }

  function addNotification(type, msg) {
      let html = document.getElementById("alertNotification").innerHTML;
      let notificationCount = document.getElementsByClassName("notificationBadge").length;
      html += `
      <div class="notificationBadge">
          <div class="` + type + `" role="alert">
              ` + msg + `
          </div>
      </div>                
      `;
      document.getElementById("alertNotification").innerHTML = html;
      // document.getElementById("notificationBadge" + notificationCount).style.opacity = 1;
      // document.getElementById("notificationBadge" + notificationCount).style.transition = "all 1s";                
      // document.getElementById("notificationBadge" + notificationCount).style.opacity = 0;
      setTimeout(function(){ 
          if(document.getElementsByClassName("notificationBadge")[notificationCount]) {
              document.getElementsByClassName("notificationBadge")[notificationCount].style.display = "none"; 
          }
      }, 4000);
  }

  return (
    <div className="SponsorFamily">
        <div className="content">
            <br />
            <div id="alertNotification"></div>                
            <h4 id="familyID">Sponsor a family</h4>
            <br />
            <p>With your donations, you will receive updates on the wellbeing of your chosen family.</p>
            <br />
            
            <div id="creditCardDiv" className="sponsorDiv">
                <h5>Your Credit Card Details</h5>
                <br />
                <PaymentSection />
                <button className="btn btn-primary" type="submit" onClick={setSponsored}>Confirm details</button>
            </div>
            
        </div>
    </div>
  );
}

export default SponsorFamily;
