import React, { useEffect} from 'react';
import { useHistory } from "react-router-dom";
import PaymentSection from './paymentSection';
import firebase from './firebase';

const DonateMoney = () => {
    // const [cardNumber, setCardnumber] = useState("");
    // const [name, setName] = useState("");

    const history = useHistory();
    
    useEffect(() => {
        let savedEmail = sessionStorage.getItem("savedEmail");        
        if (savedEmail) {
            console.log("savedmail is " + savedEmail);
            setReference(savedEmail);
            getZakaat();
        } else {
            history.push("/");
        }
    });

    function setReference(savedEmail) {
        if (document.getElementById("referenceID")) {
            if (savedEmail) {
                document.getElementById("referenceID").innerHTML = savedEmail;
            } else {
                document.getElementById("referenceID").innerHTML = "<i>Your email address</i>";
            }
        } 
    }

    async function getZakaat() {
        let z = await firebase.getZakaat();
        if (z !== 0) {
            document.getElementById("headingID").innerHTML = z.heading;
            let img = z.image;
            img = img.replaceAll(" ", "");
            img = img.split(";;");
            for (let i = 0; i < img.length; i++) {
                document.getElementById("imageID").innerHTML += `
                    <img class="sponsorSmall" src="` + img[i] + `" alt="topleft" align="right"></img>
                    <img class="sponsorLarge" src="` + img[i] + `" alt="topleft" style="display:none"></img>

                `;
                // {/* <img id="imageID" src={moneyDonate} alt="topleft" style={{width: "40%", minWidth:"350px", paddingLeft:"40px"}} align="right" /> */}
            }
            document.getElementById("imageID").src = z.image;
            document.getElementById("pID").innerHTML = z.details;
            imgEnlarge();

            let curr = z.current;
            let tar = z.target;

            document.getElementById("prog").style.width = (100*curr/tar) + "%";//So far $" + curr + " donated, target is $" + tar;
            document.getElementById("prog").innerHTML = "$" + curr;
            document.getElementById("progMsg").innerHTML = "So far $" + curr + " raised. Goal is $" + tar;

        }
    }

    function imgEnlarge() {
        let imgs = document.getElementsByClassName("sponsorSmall");
        for (let i = 0; i < imgs.length; i++) {
            imgs[i].onclick = async function(){
                document.getElementsByClassName("sponsorLarge")[i].style.display = "block";
                document.getElementById("closeImgBtn").style.display = "block";
                document.getElementById("shade").style.display = "block";
                document.getElementById("imageID").style.float = "left";
            }
        }
    }

    function closeImg() {
        if (document.getElementById("shade").style.display === "block") {
            let imgs = document.getElementsByClassName("sponsorLarge");
            for (let i = 0; i < imgs.length; i++) {
                document.getElementsByClassName("sponsorLarge")[i].style.display = "none";
            }
            document.getElementById("shade").style.display = "none";
            document.getElementById("closeImgBtn").style.display = "none";
            document.getElementById("imageID").style.float = "right";
        }        
    }

    return (
        <div className="DonateMoney">
            <div className="content">
                <br />
                <h3>Donate Money</h3>
                <br />
                <p>Thank you for your generosity. Please fill in the form to make a donation:</p>
                <br />
                <h4 id="headingID"> </h4>
                <br />
                <button id="closeImgBtn" onClick={closeImg} style={{display:"none"}}>X</button>
                <div id="shade" onClick={closeImg} style={{display:"none"}}></div>
                <div id="story">
                    {/* <img id="imageID" src={moneyDonate} alt="topleft" style={{width: "40%", minWidth:"350px", paddingLeft:"40px"}} align="right" /> */}
                    <div id="imageID"></div>
                    <p id="pID" > </p>                    
                </div>

                <br />
                <br />
                <div className="progress" style={{height:"50px", width:"50%"}} >
                  <div id="prog" className="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style={{width: "10%", fontSize:"24px"}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                </div>
                <br />
                <p id="progMsg"></p>

                <br />
                <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
                <hr></hr>
                <PaymentSection />     
                <button className="btn btn-primary" type="submit" >Confirm details</button>
            </div>
            <br />
        </div>      
    );
}

export default DonateMoney;
